@media(max-width: 1200px) {
  .kitchen-order{
    margin-top: 90px;
    margin-bottom: 60px;
    width: 100%;
    height: 100% !important;
  }
  .side-content{
    height: 450px !important;
    border: 2px solid #ffffff;
    margin: 0px 10px 0px 10px;
    background-color: #ffffff;
    border-radius: 10px;
    .list-order-re{
      font-family: Cabin;
      font-style: normal;
      font-weight: bold;
      font-size: 29px;
      line-height: 32px;
      text-align: center;
      color: #1E1C19;
      padding-top: 20px;
    }
    .ra-button-re{
      padding-top: 20px;
      .status-check-re{
      }
      .input-status-re{
        font-family: Cabin;
        font-style: normal;
        font-weight: normal;
        font-size: 21px;
        line-height: 23px;
        text-align: center;
        color: #7A7A7A;
      }
      .line-color{
        height: 5px;
        border-bottom: 3px #FCBC3A;
      }
    }
  }
  .mh-55{
    height: calc(100% - 250px) !important;
    width: auto;
    margin-top: 25px;
    margin-bottom: 15px;
  }
  .card-order{
    margin-bottom: 5px;
    height: 50px;
    width: auto;
    border: 2px solid lightcoral;
    align-items: center;
    .card-order-button{
      background-color: lightcoral;
    }
  }
}

