  .thanks-header {
    max-width: 90% !important;
  }
}

.menu-type-a-re{
  height: 100%;
  .menu-type-re{
    width: 100%;
    height: 100%;
    border: 0;
    background-color: transparent;
    padding-left: 15px;
    padding-right: 15px;
    font-size: 14px;
    position: relative;
    outline: 0;
    .header-re-icon{
      background-color: #FFEFCD !important;
      .icon-button-menu-re{
        //width: 20px;
        //height: 20px;
      }
    }
    .square-text-button-re{
      font-family: Cabin;
      font-style: normal;
      font-size: 16px;
    }
  }
}

.menu-type-a-re:hover{
  .menu-type-re{
    .header-re-icon{
      background-color: #FCBC3A !important;
    }
  }
}

.plus-background-color-re-noti{
  height: 30px;
  width: 30px;
  background-color: #FCBC3A !important;
  .plus-icon-button-re-right{
    height: 25px;
    width: 25px;
    transform: matrix(0,-1,1,0,0,0);
  }
  .plus-icon-button-re-left{
    height: 25px;
    width: 25px;
    transform: matrix(0,1,-1,0,0,0);
  }
}

.plus-icon-background-re-view-all{
  height: 20px;
  width: 20px;
  .plus-icon-button-re-view-all{
    height: 15px;
    width: 15px;
    background-color: #FCBC3A !important;
  }
}

.header-re-icon-manage-table{
  .icon-button-menu-manage-table{
    height: 25px;
