  }
}

.mh-56{
  height: 350px !important;
  width: 100%;
  margin-bottom: 35px;
}

.check-all-button-matching{
  display: none;
}
.choose-all-matching{
  margin-bottom: auto;
  margin-top: auto;
  background: #FFEFCD;
  border-radius: 30px;
  height: 35px;
  width: 100%;
  text-align: center;
  border: 2px solid #FCBC3A;
  .choose-text-matching{
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    text-align: center;
    padding-top: 3px;
    color: #1E1C19;
  }
}

input[type='checkbox'].check-all-button-matching:checked ~ div.choose-all-matching{
  background-color: #FCBC3A;
}

.previousBttn{
  border: 1px solid #FCBC3A !important;
  width: 41px !important;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.nextBttn{
  border: 1px solid #FCBC3A !important;
  width: 41px !important;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.plus-icon-button-re-left-page{
  height: 15px;
  width: 15px;
  transform: matrix(0,1,-1,0,0,0);
}

.plus-icon-button-re-right-page{
  height: 15px;
  width: 15px;
  transform: matrix(0,-1,1,0,0,0);
}

.paginationBttns {
  height: 100%;
  width: 100%;
  list-style: none;
  display: flex;
  justify-content: center;
}

.paginationBttns a {
  padding-top: 9px;
  padding-bottom: 9px;
  padding-left: 12px;
  padding-right: 12px;
  font-family: Cabin;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 20px;
  text-align: center;
  cursor: pointer;
  height: 34px !important;
  width: 34px !important;
  border: 1px solid #ced4da;
}

.paginationBttns a:hover {
  background-color: #eeeeee;
  color: #000000;
}

.paginationActive a {
  color: #ffffff!important;
  background-color: #FCBC3A;
}

.paginationActive a:hover {
  color: #ffffff!important;
  background-color: #FCBC3A;
}

.paginationDisabled a {
  color: #000000;
}

.page-item{
  &.active-paging .page-link {
    z-index: 3;
    color: #ffffff;
    background-color: #FCBC3A;
    border-color: #FCBC3A;
  }
}